html,
body {
  margin: 0;
}

#App {
  font-family: sans-serif;
  height: 100vh;
}

#page-wrap {
  text-align: center;
  overflow: auto;
}

.bm-item {
  display: inline-block;
  text-decoration: none;
  margin-bottom: 10px;
  color: #d1d1d1;
  transition: color 0.2s;
}

.bm-item:hover {
  color: white;
}

.bm-burger-button {
  position: absolute;
  width: 36px;
  height: 30px;
  left: 20px;
  top: 10px;
}

.bm-burger-bars {
  background: #373a47;
}

.bm-cross-button {
  height: 24px;
  width: 24px;
}

.bm-cross {
  background: #bdc3c7;
}

.bm-menu {
  background: #373a47;
  padding: 2.5em 1.5em 0;
  font-size: 1.15em;
}

.bm-morph-shape {
  fill: #373a47;
}

.bm-item-list {
  color: #b8b7ad;
}

.bm-overlay {
  background: rgba(0, 0, 0, 0.3);
}

.publisher {
  font-family: "Roboto", sans-serif;
  font-weight: 300;
  font-size: 15px;
}

.bi {
  position: relative;
  float: right;
}

.cover-pic {
  width: 50px;
  height: 50px;
  object-fit: cover;
  border-radius: 50%;
}

.user-name {
  margin-left: 10%;
}

.btn {
  background-color: rgba(0, 0, 0, 0);
  border: none;
  margin-top: 10%;
}

.btn:hover {
  background-color: transparent;
  border: none;
}

.dropdown-menu {
  background-color: transparent;
  border: none;
}

.dropdown-item {
  color: #ffff;
}

.menu-item {
  cursor: pointer;
}
