.cover-pic-u {
    position: relative;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    padding: 10px 30px;
    margin-top: 10%;
  }
  
  .cover-pic-u .row > img {
    height: 300px;
    width: 300px;
    border-radius: 10%;
    object-fit: cover;
  }

  .cover-pic-u .prf .i {
    height: 30px;
    width: 30px;
    border-radius: 50%;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    position: relative;
    cursor: pointer;
    bottom: 70px;
    left: 0;
    color: #000;
  }

  .cover{
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }

  .cover-pic-u{
    width: 30%;
  }

  .profile-button{
    width: 60%;
  }

  @media screen and (max-width:768px) {
    .cover-pic-u{
      width: 70%;
    }
    .profile-button{
      width: 40%;
    }
  }
  
  @media screen and (max-width:480px) {
    .cover-pic-u{
      width: 100%;
    }
  }
