$primary: #f5fffa;
$button: #306754;
$black: #000000;
$secondary: #00beff;

$lato: "Lato", sans-serif;
$roboto: "Roboto", sans-serif;
$dmsan: "DM Sans", sans-serif;

$smWidth: 420px;
$mobWidth: 480px;
$tabWidth: 768px;
$lWidth: 1024px;
$xlWidth: 1200px;

@mixin xl {
  @media (max-width: #{$xlWidth}) {
    @content;
  }
}
@mixin l {
  @media (max-width: #{$lWidth}) {
    @content;
  }
}
@mixin tab {
  @media (max-width: #{$tabWidth}) {
    @content;
  }
}
@mixin mobile {
  @media (max-width: #{$mobWidth}) {
    @content;
  }
}
@mixin sm {
  @media (max-width: #{$smWidth}) {
    @content;
  }
}


#error-page {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100vh;
}


